.card {
  position: relative;
  width: 100%;
  max-width: 21.25rem;
  aspect-ratio: 1/1;
  border-radius: 0.625rem;
  overflow: hidden;
  display: block;
  text-decoration: none;
}
.card__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.card__overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(10, 10, 10, 0) 35%, rgba(4, 4, 4, 0.41) 73%, rgb(0, 0, 0) 100%);
  z-index: 1;
}
.card__title {
  position: absolute;
  left: 1.25rem;
  bottom: 1.25rem;
  color: white;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 1.125rem;
  line-height: 1.43;
  z-index: 2;
}